/* RESET */
* {
  margin: 0;
  padding: 0;
  border: 0;
}

body {
  background-color: #1c2541;
}

nav {
  display: flex;
  position: fixed;
  height: 64px;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(12, 13, 22, 0.7450980392);
  -webkit-backdrop-filter: saturate(180%) blur(20px);
          backdrop-filter: saturate(180%) blur(20px);
  box-shadow: 1px 0px 4px #0c0d16;
  z-index: 50;
}
nav div.nav-logo a {
  display: flex;
  align-items: center;
  padding: 0 15px;
  cursor: pointer;
  text-decoration: none;
}
@media (max-width: 1023px) {
  nav div.nav-logo a {
    padding-right: 0;
    padding-left: 10px;
  }
}
nav div.nav-logo a img {
  height: 40px;
  margin-right: 7px;
  margin-top: -3px;
}
nav div.nav-logo a h2 {
  color: #eff0f2;
  font-family: Lobster, cursive;
  font-weight: 100;
  font-size: 30px;
}

main {
  padding-top: 65px;
}

.password-container {
  padding: 20px;
}
.password-container .newPasswordHidden {
  background-color: #0e1326;
  min-width: 300px;
  max-width: 500px;
  margin: 0 auto;
  padding: 0;
  border-radius: 8px;
  color: white;
  height: 0;
  overflow: hidden;
  transition: all 200ms linear;
}
.password-container .newPasswordHidden .identifier {
  display: grid;
  align-items: center;
  margin-bottom: 20px;
  grid-template-columns: 65px 1fr;
}
.password-container .newPasswordHidden .identifier img {
  width: 45px;
  background-color: white;
  border-radius: 50%;
  padding: 2px;
  margin-right: 15px;
}
.password-container .newPasswordHidden .identifier p {
  display: inline-block;
  font-size: 20px;
  font-family: Montserrat;
}
.password-container .newPasswordHidden .data-container {
  border-top: 1px solid rgba(255, 255, 255, 0.403);
  margin-bottom: 5px;
}
.password-container .newPasswordHidden .data-container p {
  font-family: Montserrat;
  font-size: 20px;
  margin: 20px 0 0 0;
}
.password-container .newPasswordHidden input {
  display: block;
  text-align: center;
  margin: auto;
  margin-top: 10px;
  width: 100%;
  padding: 8px 10px;
  width: 95%;
  box-sizing: border-box;
  background-color: #ffffff;
  font-family: Montserrat;
  font-size: 22px;
  border-radius: 12px;
  font-weight: 900;
}
.password-container .newPasswordVisible {
  background-color: #0e1326;
